let stock = createSlice({
    name : 'stock',
    initialState : ['10', '11', '12']
})

let cart = createSlice({
  name : 'cart',
  initialState : [
    {id : 0, name : 'White and Black', count : 2},
    {id : 2, name : 'Grey Yordan', count : 1}
   
  ],
  reducers : {
    addCount(state,action){ 
      //state[i].count += 1 //payload:  action  (state  action )
     // console.log(state[action.payload].count += 1);
     // console.log(state[action.payload].count++);
      //console.log(state[action.payload].id)
    /*
     let  = state.findIndex((a)=>{
      return a.id  == action.payload
     })
    state[].count++;
    */
      //console.log(state[].id)
      let number = state.findIndex((a)=>{
        return a.id  == action.payload
        })
      state[number].count++;     
      //payload   id    +1

    },
    
    addItem(state,action){
      let number = state.findIndex((a)=>{
        return a.id  === action.payload.id
       })
       if(number > -1 ) {

        state[number].count++

      } else {

        state.push(action.payload)

      }

    }
  }
})

export let {addCount, addItem} = cart.actions 



export default configureStore({
  reducer: { 
    user : user.reducer,
    stock : stock.reducer,
    cart : cart.reducer
  }
}) 

